<template>
  <div>
    <div class="teacherDiv">
      <div class="setleft">
        <el-image
          v-if="objItem.imgurl === ''"
          class="teachImg"
          :src="imgurl"
        ></el-image>
        <el-image v-else class="teachImg" :src="objItem.imgurl"></el-image>
      </div>
      <div class="setleft2">
        <table class="teachertable">
          <tr>
            <td>教师编号：</td>
            <td>
              {{ objItem.tnumber }}
            </td>

            <td>教师名字：</td>

            <td>
              {{ objItem.tname }}
            </td>
          </tr>
          <tr>
            <td>学生名字：</td>
            <td>
              {{ objItem.stuname }}
            </td>
            <td>学生编号：</td>
            <td>
              {{ objItem.stunumber }}
            </td>
          </tr>
          <tr>
            <td>课时:</td>
            <td>
              {{ objItem.tnum }}
            </td>
            <td>科目：</td>
            <td>
              {{ objItem.tsubject }}
            </td>
            <td></td>
          </tr>
          <tr>
            <td>电话：</td>
            <td>
              {{ objItem.tel }}
            </td>
            <td>
              <img
                class="telImg"
                src="../../static/imgs/me_kfphone.png"
                alt=""
              />
            </td>
            <td>
              <button v-show="objItem.isshow" @click="submit()">通过</button>
            </td>
            <td></td>
          </tr>
        </table>
      </div>
    </div>
  </div>
</template>
<style scoped>
.teachertable {
  font-size: 12px;
  text-align: right;
  width: 100%;
  height: 100px;
}
.teacherDiv {
  overflow: hidden;
}
.teachImg {
  width: 100%;
  height: 100px;
}
.setleft {
  float: left;
  width: 30%;
}
.setleft2 {
  background-color: lightcyan;
  float: left;
  width: 70%;
}
.telImg {
  width: 20px;
  height: 20px;
}
.setRight {
  /* float: right; */
}
a {
  text-decoration: none;
}
</style>

<script>
export default {
  props: {
    objItem: {
      type: Object,
      default: {
        timgurl: require("../../static/imgs/detail03.png"),
        tnumber: "123",
        tname: "大球",
        tsubject: "英语",
        ttel: "12345678998",
        stuname: "",
        stunumber: "",
        tnum: 1,
        href: "#/teacherinfor",
        isshow: true,
      },
    }
  },
    created() {
      if (typeof this.objItem.isshow == "undefined") {
        this.objItem.isshow = false;
      }
    },
    methods: {
      submit: function () {
        let stringify = JSON.stringify(this.objItem);

        this.axios
          .post(this.baseUrl + "/applyCourse/setApplyCourseStatus", stringify, {
            headers: { "Content-Type": "application/json" },
          })
          .then(function (response) {
            if (response.data.status == 200) {
              alert("添加成功");
            } else {
              alert("添加失败");
            }
          })
          .catch(function (error) {
            alert("添加失败");
          });
      },
    },
};
</script>


