<template>
    <div>
        <div class="roll3">
			<img class="roll3" :src="imgurl" alt=""/>
		</div>
			<input  id="search" class="bar7" v-model="search" type="text" placeholder="搜索...">
            <div v-for="(item, i) in filterShoppingList " :key="i">
            <teacherBlock :objItem="item"></teacherBlock>
            </div>
    </div>

</template>
<style scoped>
.bar7 {
		height: 40px;
		margin: 0 auto;
		width:90%;
		border-radius:42px;
		border:2px solid #324B4E;
		background:#F5F5F5;
		padding-left: 20px;
		}
        .roll3
        {
            width: 100%;
        }
</style>
<script>
import teacherBlock from '@/components/teacherBlock'
export default {
    data()
    {
      return{
         imgurl:require('../../../static/imgs/img_04test.png'),
        dataItems:[],
        search:'',
      }  
    },
    methods:{
        setData(t)
        {

           for(var i=0; i<t.length;i++)
            {

                let item=t[i];
                this.dataItems.push(item);

            }

        }
    },
    mounted()
    {

        let dataObj=this;
         this.axios
            .get(this.baseUrl+'/TInfo/getAllTeachers')
            .then(
                function(response)
                {
                    if(response.data.code==100)
                    {
                      // dataObj.dataItems=response.data.t;
                      dataObj.setData(response.data.t);
                    }
                    else
                    {
                         alert("失败");
                    }
                }
            )
            .catch(function (error) { // 请求失败处理
                console.log(error);
            }); 


    },
    components:{teacherBlock},
    computed: {
            filterShoppingList: function () {
            // `this` points to the vm instance
            var key = this.search;
            var shoppingList = this.dataItems;
            return shoppingList.filter(function (item) {
                //return item.toLowerCase().indexOf(key.toLowerCase()) != -1

                if(item.name.toLowerCase().indexOf(key.toLowerCase())!=-1||item.number.toLowerCase().indexOf(key.toLowerCase())!=-1||item.tel.toLowerCase().indexOf(key.toLowerCase())!=-1)
                {
                    return item;
                }

            });
            }
    }
}
</script>


