<template>
  <div>


    <el-card class="box-card">
       <input type="tel" v-model="tell" placeholder="请输入手机号">
        <input type="password" v-model="password" placeholder="请输入密码">
        <div class="button" @click="loginFunc">登  录</div>
        <p class="info">登录即代表同意《大球教育》相关规定</p>
        <p class="login-bottom">
        <span class="no" @click="register">没有账号？注册</span>
        </p>
    </el-card>



   
  </div>
</template>

<script>
export default {
  props: {},
  data () {
    return {
      tell: '',
      password: ''
    };
  },
  mounted () {

  },
  methods: {
    loginFunc () {
      
        this.axios
            .get(this.baseUrl+'/login/'+this.tell+'/'+this.password)
            .then(
                function(response)
                {
                    if(response.data.code==100)
                    {
                      // dataObj.dataItems=response.data.t;
                       var user= response.data.t;
                        localStorage.setItem('name',user.name);
                        localStorage.setItem('saying',user.nick);
                        localStorage.setItem('role',user.role);
                        
                        location.href="#/";
                    }
                    else
                    {
                         alert("失败");
                    }
                }
            )
            .catch(function (error) { // 请求失败处理
                console.log(error);
            }); 

    },
    register: function () {
      location.href = '#/register';
    }
  }
};
</script>

<style scoped >
.box-card {
    width: 100%;
    margin-top: 100px;
  }
input {
  width: 100%;
  height: 50px;
  border-top: 0px solid #fff;
  border-left: 0px solid #fff;
  border-bottom: 1px solid #eaeaea;
  outline: none;
  text-indent: rem(20);
  font-size: rem(15);
  background: #fff;
  box-sizing: border-box;
}
.button {
  width: 100%;
  height: 50px;
  background-color: #2ebd51;
  font-size: rem(15);
  margin-top: rem(20);
  border-radius: rem(2);
  text-align: center;
  vertical-align: middle;
  line-height: 50px;
}
.info {
  font-size: rem(12);
  text-align: center;
  color: #bcbcbc;
  margin-top: rem(10);
}
.login-bottom {
  height: rem(20);
  margin-top: rem(15);
  width: 94%;
  margin-left: 3%;
}
.login-bottom span {
  text-decoration: underline;
  color: #666;
  font-size: rem(12);
  display: inline-block;
}
.forget {
  float: right;
}
.no {
  float: left;
}
</style>



