<template>
    <div>
        <h1>教师授权管理</h1>
        <el-table
    :data="tableData"
    style="width: 100%"
    height="500">
      <el-table-column
      fixed
      prop="name"
      label="姓名"
      width="80">
    </el-table-column>

    <el-table-column
      prop="number"
      label="编号"
      width="80">
    </el-table-column>
  
    <el-table-column
      prop="subject"
      label="科目"
      width="60">
    </el-table-column>
    <el-table-column
      prop="tel"
      label="电话"
      width="100">
    </el-table-column>


    <el-table-column
      label="授权"
      width="60">
        <template slot-scope="scope">
        <el-button
          @click.native.prevent="agreefun(scope.$index)"
          type="text"
          size="small">
          同意
        </el-button>
      </template>
    </el-table-column>
   
    <el-table-column
      label="操作"
      width="120">
      <template slot-scope="scope">
        <el-button
          @click.native.prevent="deletefun(scope.$index)"
          type="text"
          size="small">
          移除
        </el-button>
      </template>
    </el-table-column>




  </el-table>

    </div>
</template>

<style scoped>

</style>


<script>
export default {
     data() {
      return {
        tableData: []
      }
    },
    methods:{

        SetItem(t)
        {
            for(let i=0;i<t.length;i++)
            {   
                var item=t[i];
                item.authen='允许';
                item.delete='删除';
                this.tableData.push(item);
            }
        },
        agreefun(index)
        {
            this.$confirm('此操作将同意授权, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
         this.agree(index);
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
           
          });          
        });
        
        },
        agree(index)
        {
           // index--;

            if(index<0)
            {
                return;
            }
            var num=this.tableData[index].id;
            this.axios
            .get(this.baseUrl+'/T/setUnAuthen/'+num)
            .then(
                function(response)
                {
                    if(response.data.code==100)
                    {
                        alert("成功");

                    }
                    else
                    {
                         alert("失败");
                    }
                }
            )
            .catch(function (error) { // 请求失败处理
                console.log(error);
            });
        },
        deletefun(index)
        {

            this.$confirm('此操作将永久删除, 是否继续?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
            }).then(() => {
            this.deleteRow(index);
            }).catch(() => {
            this.$message({
                type: 'info',
                message: '已取消删除'
            
            });          
            });
        },

        deleteRow(index)
        {

            if(index<0)
            {
                return;
            }
            var num=this.tableData[index].id;
            this.axios
            .get(this.baseUrl+'/T/delete/'+num)
            .then(
                function(response)
                {
                    if(response.data.code==100)
                    {
                        alert("成功");
                        this.tableData.splice(index,1);
                    }
                    else
                    {
                         alert("失败");
                    }
                }
            )
            .catch(function (error) { // 请求失败处理
                console.log(error);
            }); 
        }

    },
    mounted()
    {

        let t=null;
        let obj=this;
    this.axios
      .get(this.baseUrl+'/T/getUnAuthen')
      .then(
          function(response)
          {
              if(response.data.code==100)
              {
                    t=response.data.t;
                    obj.SetItem(t);
              }
              else
              {
                  alert("请求失败")
              }
              //console.log(response);
          }
      )
      .catch(function (error) { // 请求失败处理
        console.log(error);
      });
       // this.tableData


    }
}
</script>


