<template>
    <div>
    <input  id="search" class="bar7" v-model="search" type="text" placeholder="搜索..."/>
    <div v-for="(item, i) in filterShoppingList " :key="i">
        <courseItem :objItem="item"></courseItem>
    </div>
    </div>
</template>
<style>
	.bar7 {
		height: 40px;
		margin: 0 auto;
		width:90%;
		border-radius:42px;
		border:2px solid #324B4E;
		background:#F5F5F5;
		padding-left: 20px;
			}
  .el-table .warning-row {
    background: oldlace;
  }

  .el-table .success-row {
    background: #f0f9eb;
  }
</style>
<script>
import courseItem from "@/components/ApplyCourseItem"
export default {
     data()
    {
      return{
         imgurl:require('../../../static/imgs/img_04test.png'),
        dataItems:[],
        search:'',
      }  
    },
       methods:{
        setData(t)
        {

           for(var i=0; i<t.length;i++)
            {

                let item=t[i];
                item.isshow=false;
                this.dataItems.push(item);

            }

        },
    },
       computed: {
            filterShoppingList: function () {
            // `this` points to the vm instance
            var key = this.search;
            var shoppingList = this.dataItems;
            return shoppingList.filter(function (item) {
                //return item.toLowerCase().indexOf(key.toLowerCase()) != -1

                if(item.tname.toLowerCase().indexOf(key.toLowerCase())!=-1||item.tnumber.toLowerCase().indexOf(key.toLowerCase())!=-1)
                {
                    return item;
                }

            });
            }
    },
    mounted()
    {


      
      let dataObj=this;
         this.axios
            .get(this.baseUrl+'/applyCourse/getAllApplyCoursesWithPass')
            .then(
                function(response)
                {
                    if(response.data.status==200)
                    {
                      // dataObj.dataItems=response.data.t;
                      dataObj.setData(response.data.data);
                    }
                    else
                    {
                         alert("失败");
                    }
                }
            )
            .catch(function (error) { // 请求失败处理
                console.log(error);
            }); 

    },
    components:{courseItem}

}
</script>


