<template>
    <div>



        <el-card class="box-card">
         <el-input v-model="name" type="text" placeholder="请输入名字"></el-input>
        <el-input v-model="subject" type="text" placeholder="请输入科目"></el-input> 
         <el-input v-model="number" type="text" placeholder="请输入编号"></el-input>       
        <el-input v-model="tel" type="tel" placeholder="请输入手机号"></el-input>
        <el-input  type="password" v-model="pwd" placeholder="请输入密码"></el-input>
        <el-input  type="password" v-model="secondPwd" placeholder="请输入确认密码"></el-input>

        <el-button type="success" @click="submit()" class="btn" round>申请</el-button>
      
        </el-card>




  </div>
      
</template>

<style scoped>

.btn
{
    width: 100%;
}
 .text {
    font-size: 14px;
  }

  .item {
    padding: 18px 0;
  }

  .box-card {
    width: 100%;

  }
</style>

<script>
export default {
  data() {
    return {
        number:'',
     name:'',
     subject:'',
     tel:'',
     pwd:'',
     secondPwd:''
    }  
  },
  methods:{
     submit()
     {

          let pass=true;
          if(this.pwd!=this.secondPwd)
          {
            pass=false;
            alert("两次密码不同");
          }
          if(this.tel.length<6)
          {
             pass=false;
             alert("密码长度必须大于6");
          }
          if(this.name.trim()===""||this.subject.trim()===""||this.number.trim()==="",this.tel.trim()==="")
          {
             pass=false;
              alert("名字，科目，编号，电话不能为空");
          }
          if(!pass)
          {
            return;
          }
          var data={};
          data.name=this.name;
          data.pwd=this.pwd;
          data.subject=this.subject;
          data.number=this.number;
          data.tel=this.tel;

          let stringify = JSON.stringify(data);

          this.axios.post(this.baseUrl+'/T/Add',stringify,{headers: {'Content-Type': 'application/json'}}).then(function (response) {
              if(response.data.code==100)
              {
                alert("申请成功");
              }
              else
              {
                 alert("申请失败");
              }
          }).catch(function (error) {
              alert("申请失败");
          });
      },
  }

}
</script>


