<template>
    <div>
        <teacherBlock :objItem="Item" :isshow=false></teacherBlock>
        <el-card class="box-card">
            <div slot="header" class="clearfix">
            <div id="profile">个人简介</div>
            </div>
            <div id='conetent'>
                {{conetent}}
            </div>
        </el-card>

        <el-card class="box-card">
            <div slot="header" class="clearfix">
            <div id="profile">教学经历</div>
            </div>
            <div id='conetent'>
                {{educationInfo}}
            </div>
        </el-card>

        <el-card class="box-card">
            <div slot="header" class="clearfix">
            <div id="profile">成功案例</div>
            </div>
            <div id='conetent'>
                {{secusscase}}
            </div>
        </el-card>

    </div>
</template>

<style scoped>
  .text {
    font-size: 14px;
  }
#profile,#conetent
{
    text-align: left;
    margin-left: 3px;
}
#conetent
{
     font-size: 12px;
    text-align: left;
    margin-left: 3px;
}
  .item {
    margin-bottom: 18px;
  }
  .clearfix:after {
    clear: both
  }

  .box-card {
    margin-top: 8px;
    width: 100%;
  }
</style>

<script>
import teacherBlock from '@/components/teacherBlock'
export default {
    data()
    {
        return{conetent:'这是简介内容',educationInfo:'这是教学经历',secusscase:'这是成功案例',Item:{name:'',subject:'',number:'',tel:''}}
    },
    components:{teacherBlock},
    mounted()
    {
        let id = this.$route.params.id
       this.$http.get(this.baseUrl+'/TInfo/getTeacher/'+id)
                    .then(function(res){
                             if(res.data.code==100)
                             {
                               this.parseData(res.data.t);
                             }
                    },function(){
                        alert('请求失败');
                });
    },
    methods:{

      parseData(data)
      {
        this.conetent=data.profile;
        this.educationInfo=data.experience;
        this.secusscase=data.successfulCase;
        this.Item.name=data.name;
        this.Item.tel=data.tel;
        this.Item.number=data.number;
        this.Item.imgurl=data.imgurl;
        this.Item.subject=data.subject;
      }
    }

}
</script>


