<template>
  <div>
    <el-radio-group v-model="labelPosition" size="small">
    <el-radio-button label="left">左对齐</el-radio-button>
    <el-radio-button label="right">右对齐</el-radio-button>
    <el-radio-button label="top">顶部对齐</el-radio-button>
    </el-radio-group>
    <div style="margin: 20px;"></div>
    <el-form :label-position="labelPosition" label-width="80px" :model="student">
    <el-form-item label="名字">
        <el-input v-model="student.name"></el-input>
    </el-form-item>
    <el-form-item label="学号">
        <el-input v-model="student.number"></el-input>
    </el-form-item>
    </el-form>

    <el-button class="add"  @click="addSubject()"  type="primary" icon="el-icon-circle-plus"></el-button>
    <span class="add m">添加科目成绩</span>
  
  <div class="item" v-for="(item, i) in student.subjects" :key="i">
  
<el-select class="subject" v-model="item.subject"  placeholder="请选择科目">
    <el-option
      v-for="item in options"
      :key="item.key"
      :value="item.label">
    </el-option>
  </el-select>

    <el-input v-model="item.score" width="30%" type="Number" placeholder="请输入分数"></el-input>
  
    <el-button @click="deleteItem(item)" class="el-icon-delete"></el-button>
  </div>
  <el-button class="submit" type="success" @click="add()" round> 提交</el-button>
</div>
</template>
<style scoped>
.add
{
    margin-left: 20px;
    float: left;
    margin-bottom:10px;
}
.m
{
    margin-top: 8px;
}
.item
{
    clear: both;

}
.submit
{
  clear:both;
  width:100%;
  margin-top:20px;
}
.item >*
{
  float:left;
    
}
.subject
{
  width:30%;
}
.el-input
{
  width:50%!important;
  margin-left:10px;
}
</style>
<script>
  export default {
    methods: {
      addSubject()
      {
        this.student.subjects.push({});
      },
      add()
      {
          var student=this.student;
          
          let stringify = JSON.stringify(student);
      
        this.axios.post(this.baseUrl+'/score/add', stringify,{headers: {'Content-Type': 'application/json'}})
        .then(function (response) {
         if(response.data.code==100)
         {
             alert('添加成功');
         }
         else
         {
              alert('添加失败')
         }
        })
        .catch(function (error) {
           alert('添加失败')
        });
 
      },
      deleteItem(data)
      {
          let index=this.student.subjects.indexOf(data);
          this.student.subjects.splice(index,1);
      }
    },
    data() {
      return {
        labelPosition: 'right',
        student: {
          name: '',
          number: '',
          subjects:[
            {
              subject:'',
              score:0,
            }
          ]
          
        },
  options: [{
          key:'0',
          label: '数学'
        }, {
          key:'1',
          
          label: '语文'
        }, {
          key:'2',
        
          label: '英语'
        }, {
          key:'3',
         
          label: '政治'
        }
        , {
          key:'4',
         
          label: '物理'
        }
        , {
          key:'5',
         
          label: '化学'
        }
        , {
          key:'6',
         
          label: '生物'
        },
         {
           key:'7',
          label: '地理'
        }
        , {
           key:'8',
          label: '历史'
        }],
        svalue:'',
        score:0,
      };
    }
    
  }
</script>


