<template>
    <div>

    <el-radio-group v-model="labelPosition" size="small">
  <el-radio-button label="left">左对齐</el-radio-button>
  <el-radio-button label="right">右对齐</el-radio-button>
  <el-radio-button label="top">顶部对齐</el-radio-button>
</el-radio-group>
<div style="margin: 20px;"></div>
<el-form :label-position="labelPosition" label-width="80px" :model="formLabelAlign">
  <el-form-item label="教师名字">
    <el-input v-model="tname"></el-input>
  </el-form-item>
  <el-form-item label="教师编号">
    <el-input v-model="tnumber"></el-input>
  </el-form-item>
  <el-form-item label="学生名字">
    <el-input v-model="stuname"></el-input>
  </el-form-item>
  <el-form-item label="学生编号">
    <el-input v-model="stunumber"></el-input>
  </el-form-item>
   <el-form-item label="科目">
    <el-input v-model="tsubject"></el-input>
  </el-form-item>
  <el-form-item label="课时">
    <template>
  <el-input-number v-model="tnum" :min="1" :max="10"  class="btn" label="描述文字"></el-input-number>
    </template>
  </el-form-item>

 <el-form-item label="日期">
    <template>
    <el-date-picker id="date"
      v-model="date"
      type="date"
      placeholder="选择日期">
    </el-date-picker>
    </template>
  </el-form-item>



</el-form>
  <el-button type="success" @click="submit()" class="btn" round>申请</el-button>
    </div>
</template>

<style scoped>
.btn{

width: 100%;

}
#date{
  width: 100%;
}
</style>
<script>
  export default {
    data() {
      return {
        tname:'',
        tnumber:'',
        stuname:'',
        stunumber:'',
        tsubject:'',
        tnum:1,
        date:'',
        labelPosition: 'right',
        formLabelAlign: {
          name: '',
          region: '',
          type: ''
        }
      };
    },
    methods: {
     submit()
     {
        var student={};
          student.tname=this.tname;
          student.tnumber=this.tnumber;
          student.stuname=this.stuname;
          student.stunumber=this.stunumber;
          student.tnum=this.tnum;
          student.tsubject=this.tsubject;
          student.date=this.date;
        let stringify = JSON.stringify(student);
      
        this.axios.post(this.baseUrl+'/applyCourse/addApplyCourse', stringify,{headers: {'Content-Type': 'application/json'}})
        .then(function (response) {
         if(response.data.status==200)
         {
             alert('添加成功');
         }
         else
         {
              alert('添加失败')
         }
        })
        .catch(function (error) {
           alert('添加失败')
        });

     },

    }
  }
</script>


