<template>
<div> 

<input  id="search" class="bar7" type="text"  v-model="search" placeholder="搜索...">
  <el-table
    :data="filterShoppingList"
    style="width: 100%"
    :row-class-name="tableRowClassName">
    <el-table-column
      prop="number"
      label="学号"
      width="120">
    </el-table-column>
    <el-table-column
      prop="name"
      label="姓名"
      width="100">
    </el-table-column>
     <el-table-column
        prop="ident"
        label="身份证"
         width="230">
      </el-table-column>
     
     <el-table-column 
      label="操作">
       <template slot-scope="scope">
        <button @click="operation(scope.row)">操作</button>
      </template>
     
    </el-table-column>

  </el-table>



<el-dialog
  title="提示"
  :visible.sync="dialogVisible"
  width="95%"
  :before-close="handleClose">
  <span> 添加学号</span>

<el-radio-group v-model="labelPosition" size="small">
  <el-radio-button label="left">左对齐</el-radio-button>
  <el-radio-button label="right">右对齐</el-radio-button>
  <el-radio-button label="top">顶部对齐</el-radio-button>
</el-radio-group>
<div style="margin: 20px;"></div>
<el-form :label-position="labelPosition" label-width="80px" :model="stu">
  <el-form-item label="身份证">
    <el-input disabled=true v-model="stu.ident"></el-input>
  </el-form-item>
  <el-form-item disabled=true label="名字">
    <el-input v-model="stu.name"></el-input>
  </el-form-item>
  <el-form-item label="学号">
    <el-input v-model="stu.number"></el-input>
  </el-form-item>
</el-form>



  <span slot="footer" class="dialog-footer">
    <el-button @click="dialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="submit(stu)">确 定</el-button>
  </span>
</el-dialog>










</div>

  
</template>

<style>
	.bar7 {
		height: 40px;
		margin: 0 auto;
		width:90%;
		border-radius:42px;
		border:2px solid #324B4E;
		background:#F5F5F5;
		padding-left: 20px;
			}
  .el-table .warning-row {
    background: oldlace;
  }

  .el-table .success-row {
    background: #f0f9eb;
  }
</style>

<script>
  export default {
    methods: {
      operation(item)
      {
        this.dialogVisible = true
        this.stu=item;
      },
      tableRowClassName({row, rowIndex}) {
        if (rowIndex === 1) {
          return 'warning-row';
        } else if (rowIndex === 3) {
          return 'success-row';
        }
        return '';
      },
      submit(stu)
      {

        this.dialogVisible = false;

          let stringify = JSON.stringify(stu);
      
        this.axios.post(this.baseUrl+'/student/updateStudentNumber', stringify,{headers: {'Content-Type': 'application/json'}})
        .then(function (response) {
         if(response.data.status==200)
         {
             alert('添加成功');
         }
         else
         {
              alert('添加失败')
         }
        })
        .catch(function (error) {
           alert('添加失败')
        });


      },
        setData(t)
        {

           for(var i=0; i<t.length;i++)
            {

                let item=t[i];
                this.tableData.push(item);

            }

        },
          handleClose(done) {
        this.$confirm('确认关闭？')
          .then(_ => {
            done();
          })
          .catch(_ => {});
      }


    },
       computed: {
            filterShoppingList: function () {
            // `this` points to the vm instance
            var key = this.search;
            var shoppingList = this.tableData;
            return shoppingList.filter(function (item) {
                //return item.toLowerCase().indexOf(key.toLowerCase()) != -1

                if(item.name.toLowerCase().indexOf(key.toLowerCase())!=-1||item.ident.toLowerCase().indexOf(key.toLowerCase())!=-1)
                {
                    return item;
                }

            });
            }
    },
    data() {
      return {
        tableData: [],
        search:'',
        dialogVisible: false,
        labelPosition: 'right',
        stu: {
          name: '',
          ident: '',
          number: ''
        }
      }
    },
    mounted()
    {
      let dataObj=this;
         this.axios
            .get(this.baseUrl+'/student/getAllStudents')
            .then(
                function(response)
                {
                    if(response.data.code==100)
                    {
                      // dataObj.dataItems=response.data.t;
                      dataObj.setData(response.data.t);
                    }
                    else
                    {
                         alert("失败");
                    }
                }
            )
            .catch(function (error) { // 请求失败处理
                console.log(error);
            }); 

    }
  }
</script>


