<template>
  <div>
    <el-card class="box-card">
      <el-input v-model="ident" type="tel" placeholder="身份证"></el-input>
      <el-input type="text" v-model="name" placeholder="名字"></el-input>
      <el-button type="success" class="btn" round @click="register()">   申请</el-button>
    </el-card>
  </div>
</template>

<style scoped>
.btn {
  width: 100%;
}
.text {
  font-size: 14px;
}

.item {
  padding: 18px 0;
}

.box-card {
  width: 100%;
}
</style>

<script>
export default {
  data() {
    return {
      name: "",
      ident: "",
    };
  },
  methods: {
    register() {
     
		var student={};
		student.name=this.name;
		student.ident=this.ident;
		let stringify = JSON.stringify(student);
      
        this.axios.post(this.baseUrl+'/student/addStudent', stringify,{headers: {'Content-Type': 'application/json'}})
        .then(function (response) {
         if(response.data.code==100)
         {
             alert('添加成功');
         }
         else
         {
              alert('添加失败')
         }
        })
        .catch(function (error) {
           alert('添加失败')
        });
    },
  },
};
</script>


